.akilia{
    width: 100%;
    height: 100%;
    background-color: #4ec4ac;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    justify-items: center;
    align-items: center;
} 
.titelia{
    color: #000000;
    text-align: center;
    width: 100%;
}
.news-items{
    justify-content: center;
    align-items: center;
}
.mizi{
    margin-left: 42%;
}
.downloads{
    margin-left: 42%;
    width: 300px;
    height: 40px;
    background-color: #000000;
}
.des{
    color: #000000;
}
.news-item{
    display: flex;
    flex-direction: column;
    justify-content:last baseline
}

  
  /* Medium phones (iPhone 14, 15, modern Androids) */
  @media only screen and (min-width: 375px) and (max-width: 430px) {
    .container{
        width: 100%;
    }
    .news-items{
        margin-top: -15px;
        width: 100%;
        height: calc(100% + 30%);
    }
    .mizi{
        width: 300px;
        height: 300px;
        margin-left: 15%;
    }
    .des{
        font-size: 16px;
    }
    .downloads{
        margin-left: 15%;
        padding-bottom: 20px;
    }
  }
  
  /* Large phones (iPhone 14 Plus/Pro Max, larger Androids) */