.sidebar{
    display: flex;
    flex-direction: column;
    padding: 12px;
}
.sidebar button{
    filter: none;
    -webkit-filter: none;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    border-color: aquamarine;
}
.sidebar button:hover{
    transform: scale(1.1);
    -webkit-transform: scale(1.1);
    -moz-transform: scale(1.1);
    -ms-transform: scale(1.1);
    -o-transform: scale(1.1);
    transition: all 0.33s linear;
    -webkit-transition: all 0.33s linear;
    -moz-transition: all 0.33s linear;
    -ms-transition: all 0.33s linear;
    -o-transition: all 0.33s linear;
}
.sidebar button:active{
    box-shadow: 2px 2px #bb33ee;
}
.content-area{
    padding: 20px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: lighter;
    display: flex;
    flex-direction: column;
    border: 3px solid salmon;
}
.toper{
    border-bottom: 2px solid silver;
    letter-spacing: 4px;
}
input{
    color: blueviolet;
}

  /* Medium phones (iPhone 14, 15, modern Androids) */
  @media only screen and (min-width: 375px) and (max-width: 430px) {
     /* Stack sidebar and content on mobile */
     .container {
        flex-direction: column;
    }

    .sidebar {
        width: 100%;  /* Sidebar takes full width */
        padding: 10px;
        margin-bottom: 20px;  /* Add space between sidebar and content */
    }
    .sidebar button {
        width: 100px;
        font-size: 14px;
    }

    .content-area {
        width: 100%;
    }

    .menu-items {
        grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));  /* Smaller grid for mobile */
        gap: 10px;
    }
  }
  
