body{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  overflow-y: scroll;
  background-color: aquamarine;
}
html{
  scroll-behavior: smooth;
  width: 100%;
  height: 100%;
}
#root {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: black;
}
 
/* Small phones (iPhone SE, older Android devices) */
@media only screen and (max-width: 320px) {
  body {
    /* Styles for very small phones */
    width: 100%;
    justify-content: left;
  }
  html{
    scroll-behavior: smooth;
    width: 100%;
  }
  #root {
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: rgb(117, 34, 34);
  }
}

/* Medium phones (iPhone 14, 15, modern Androids) */
@media only screen and (min-width: 375px) and (max-width: 430px) {
  /* Ensure body takes up the full height */
  html, body {
    width: 100%;
    height: 100%;
    margin: 0; /* Reset default margin */
    padding: 0;
    background-color: rgb(253, 253, 253);
    scroll-behavior: smooth;
  }
  
  #root {
    width: 100%;
    min-width: 375px;
    max-width: 430px;
    height: 100%; /* Ensure it takes full height */
    display: flex; /* Use flex or block instead of contents */
    flex-direction: column;
    background-color: rgb(146, 0, 0);
  }
}

/* Large phones (iPhone 14 Plus/Pro Max, larger Androids) */
@media only screen and (min-width: 431px) and (max-width: 768px) {
  body {
    /* Styles for larger phones or phablets */
    width: 100%;
    background-color: rgb(235, 235, 235);
    justify-content: left;
  }
  html{
    scroll-behavior: smooth;
    width: 100%;
  }
  #root {
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: rgb(92, 28, 28);
  }
}

