.checkout-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(35, 74, 83, 0.767);
    display: flex;
    justify-content: center;
    align-items: center;
}

.checkout-content {
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    width: 60%;
    max-width: 90%;
    text-align: center;
    overflow-y: scroll;
    overflow-x: scroll;
}

.indi{
    margin-bottom: 15px;
}
.kauf{
    width: 100%;
    height: auto;
    padding: 12px;
    font-size: 25px;
    font-style: normal;
    list-style: none;
    border: 3px solid green;
    text-align: left;
    display: flex;
    flex-direction: column;
}
.checkout-name, .checkout-address, .checkout-phone{
    width: 100%;
    height: auto;
    font-size: 25px;
    font-style: bold;
}
.checkout-name-input, .checkout-address-input, .checkout-phone-input{
   width: 400px;
   height: 30px;
   color: #000000;
   padding-left: 12px;
   margin-left: 12px;
}
.items-list{
    display: flex;
    flex-direction: column;
}
.ki{
    font-size: 20px;
}

.payment-option, .order-option {
    display: block;
    margin: 10px 0;
    font-size: 16px;
}

.payment-option input, .order-option input {
    margin-right: 10px;
}


button {
    padding: 10px 20px;
    margin-top: 15px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
}

button.close-btn {
    background-color: #f44336;
}
.close-checkout-button{
    background-color: #f44336;
}

button.stripe-btn, button.cash-btn {
    width: 100%;
}

button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}
strong, p{
    color: green;
    font-size: 20px;
}
.payment-methods{
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: top center;
    align-items: center;
    padding-bottom: 23px;
}
.payment-selection{
    width: 100%;
    display: inline;
    color: #f44336;
}
.payment-label{
    display: inline-flex;
    font-size: 26px;
    text-transform: uppercase;
    width: 33.33%;
    text-align: center;
    justify-content: center;
    align-items: center;
    align-content: center;
}
.order-type{
    width: 100%;
    height: 40px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;
    align-items: center;
}
.payment-Paypal{
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    justify-items: center;
    align-content: center;
    padding-bottom: 13px;
}
.stripe-container{
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    padding-bottom: 13px;
}

/* Small phones (iPhone SE, older Android devices) */
  
  /* Medium phones (iPhone 14, 15, modern Androids) */
  @media only screen and (min-width: 375px) and (max-width: 430px) {
    .checkout-content{
        width: 100%;
        height: 100%;
    }
  }
  
  /* Large phones (iPhone 14 Plus/Pro Max, larger Androids) */